import '../styles/style.css';
import { initRouter } from './router';
import 'leaflet/dist/leaflet.css';
import initPushNotification from './push-notification-init.js';
import { subscribePush } from './push-subscription';

document.addEventListener('DOMContentLoaded', async () => {
  initPushNotification(); // Register service worker

  // Request Notification Permission
  if ('Notification' in window && 'serviceWorker' in navigator) {
    const permission = await Notification.requestPermission();
    console.log('Notification permission:', permission);

    if (permission === 'granted') {
      // Register service worker first
      const registration = await navigator.serviceWorker.ready;

      // Subscribe the user and send subscription to server
      const token = localStorage.getItem('token');
      if (token) {
        try {
          await subscribePush(registration, token); // This sends it to Dicoding's API
          console.log('Push subscribed and sent to server.');
        } catch (err) {
          console.error('Failed to subscribe push:', err);
        }
      }
    }
  }

  initRouter();

  // Skip link
  const skipLink = document.querySelector('.skip-link');
  skipLink.addEventListener('click', (e) => {
    const target = document.getElementById('#');
    if (target) {
      e.preventDefault();
      target.setAttribute('tabindex', '-1');
      target.focus();
    }
  });

  // View Transition Hook
  setupViewTransitionNavigation();
});

function setupViewTransitionNavigation() {
  const navLinks = document.querySelectorAll('a[href^="#"]');

  navLinks.forEach((link) => {
    link.addEventListener('click', (e) => {
      const targetHref = link.getAttribute('href');

      if (!targetHref.startsWith('#')) return;

      const destination = targetHref.slice(1);
      const container = document.getElementById('app');

      if (!container || !document.startViewTransition) return;

      e.preventDefault();

      document.startViewTransition(async () => {
        // Simulasikan penggantian konten
        window.location.hash = destination;
        // Biarkan router handle penggantian halaman
        await initRouter();
      });
    });
  });
}

// Fungsi tambahan
export function showFormattedDate(date, locale = 'en-US', options = {}) {
  return new Date(date).toLocaleDateString(locale, {
    year: 'numeric',
    month: 'long',
    day: 'numeric',
    ...options,
  });
}

export function sleep(time = 1000) {
  return new Promise((resolve) => setTimeout(resolve, time));
}
